<template>
  <el-dialog class="login-popup" :visible="showDialog" @close="onClose()" width="630px" lock-scroll
  >
    <div class="title" slot="title"></div>
    <div class="content">
          <div class="left-bar">

    </div>
    <div class="right-content">
      <div class="input-area" v-if="!showRegisterSuccess">
        <div class="title">
          {{showRegister?'創建帳號':"會員登入"}}
          <div class="switch">
            {{showRegister?'已經是會員了？':"還不是會員"}}
            <div class="btn" @click="showRegister = !showRegister">{{showRegister?'會員登入':"註冊會員"}}</div>
          </div>
        </div>
        <div>  
          <div class="social">
            <el-button id="FB_login" class="btn btn-large btn-primary" 
            @click="fbLogin">FB 登入</el-button> <el-button id="FB_logout" class="btn btn-large btn-warning">Google 登入</el-button>
          </div> 
          <div class="or">或</div>
          <p class="err" v-if="showErr">帳號或密碼錯誤，請確認</p>
          <el-form v-if="!showRegister" :model="loginForm" :rules="loginRules" ref="loginForm">
              <el-form-item placeholder="信箱" prop="email">
                  <el-input v-model="loginForm.email"></el-input>
              </el-form-item>
              <el-form-item placeholder="密碼" prop="password">
                  <el-input type="password" v-model="loginForm.password"></el-input>
              </el-form-item>
              <el-form-item> 
                <div class="btn-group">
                  <el-button type="primary" @click="doLogin">登入</el-button>
                  <el-button @click="onClose">取消</el-button>
                </div>
            </el-form-item>
          </el-form>
          <registerPopup v-if="showRegister" @registerSuccess="registerSuccess"></registerPopup>
      </div>
    
      </div>
      <div class="success" v-else>
        <p class="success-title"> 註冊成功</p>
        <p class="desc">請至註冊信箱收認證信完成註冊流程</p>
    </div>
    </div>
    </div>
    
  </el-dialog>
</template>

<script>
import { mapState,mapActions } from 'vuex'
import  registerPopup from './registerPopup'
export default {
    props: {
        showDialog: {
            type: Boolean,
            require: true
        },
    },
    components:{
      registerPopup
    },
    data(){
      var validateEmail = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('請輸入email'));
        } else if (value.search(/^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/)== -1) {
          callback(new Error('email格式不正確'));
        } else {
          callback();
        }
      };
      var validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else if (value.length<6 || value.length>10) {
          callback(new Error('請輸入6-10位的密碼'));
        } else {
          callback();
        }
      };
      return{
        loginSuccess: false,
        loginForm:{
          email:"winny@gmail.com",
          password:"123456",
        },
        loginRules: {
          email: [
            { validator: validateEmail, trigger: 'blur' }
          ],
          password: [
            { validator: validatePassword, trigger: 'blur' }
          ],
        },
        showErr: false,
        showRegister: false,
        showRegisterSuccess: false,
      }
    },
    mounted(){
        
    },
    computed:{
      ...mapState({
            userImg: state => state.user.userImg,
            userName: state => state.user.userName,
        }),
        isLogin(){
            return this.$store.state.user.userName?true:false
        }
    },
    methods:{
        ...mapActions('user', ['fbLogin','userLogin']),
      async doLogin(){
        await this.userLogin(this.loginForm)
        this.onClose()
        window.location.reload()
      },
      registerSuccess(val){
        this.showRegisterSuccess = true
        this.loginForm = val
      },
      onClose(){
        if (this.showRegisterSuccess){
          this.doLogin()
        }
        this.$hideLoginPopup()
      }
    }
    
}
</script>

