<template>
    <div class="navbar-wrapper">
        <div class="nav-bar" :class="{'home-style':curPath === '/home'}">
            <div class="logo" @click="toPage('home')"><img src="../assets/logog.png" alt=""></div>
            <div class="nav-item-group">
                <!-- <div class="nav-item" @click="toPage('socialmedia')" :class="{'active':curPath === '/socialmedia'}">社 群</div>
                <div class="nav-item" @click="toPage('video')" :class="{'active':curPath === '/video'}">影 片</div>
                <div class="nav-item" @click="toPage('shop')" :class="{'active':curPath === '/shop'}">購 物</div> -->
                <div class="nav-item" @click="toPage('exchange')" :class="{'active':curPath === '/exchange'}">交 換</div>
                <div class="nav-item" @click="toPage('together')" :class="{'active':curPath === '/together'}">湊 團</div>
            </div>
            <div class="user-btn-group">
                <div class="btn add" @click="toggleAddSelection()" :class="{'active':showAddSelection}">
                    <img src="../assets/icon/add.svg" alt="">
                    <div class="add-selection" v-if="showAddSelection">
                        <div class="selection" @click="openAddExchange()">新增交換</div>
                        <div class="selection">新增揪團</div>
                    </div>
                </div>
                <div class="btn user" @click="toMyPage()"><img v-if="!isLogin" src="../assets/icon/user.svg" alt=""><img v-else :src="userImg" alt=""></div>
                <div class="btn message" v-if="isLogin" @click="toPage('message')"><img src="../assets/icon/message.svg" alt=""></div>
                <div class="btn logout" v-if="isLogin" @click="logout()">登出</div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState,mapActions } from 'vuex'
export default {
    data(){
        return {
            showAddSelection: false
        }
    },
    computed:{
        ...mapState({
            userImg: state => state.user.userImg,
            userName: state => state.user.userName,
        }),
        curPath(){
            return this.$route.path
        },
        isLogin(){
            return this.$store.state.user.userName?true:false
        }
    },
    methods:{
        toPage(path){
            this.$router.push(path)
            this.showAddSelection = false
        },
        toMyPage(){
            this.showAddSelection = false
            if (this.isLogin){
                this.toPage('/mine')
            } else {
                this.$showLoginPopup()
            }
        },
        toggleAddSelection(){
            console.log(1)
            this.showAddSelection = !this.showAddSelection
        },
        openAddExchange(){
            this.$store.commit('user/openAddExchange')
        },
        logout(){
            localStorage.removeItem("userName")
            window.location.reload()
        }
    }

}
</script>

